import { Link } from 'react-router-dom'
import '../css/Main.css'

export const Main = ()=>{
    
    const handleScroll = (sectionId, offset=0)=>{
        const element = document.getElementById(sectionId);
        if (element) {
          const elementPosition = element.offsetTop - offset;
          window.scrollTo({
          top: elementPosition,
          behavior: 'smooth'
          });
        } else {
          console.error(`Element with id '${sectionId}' not found.`);
        }
    }


    return(
        <div className="main" id='main'>
            <div className='section1 row'>
                <div className="categoryWrap col-3">
                    <div className="categoryBox">
                        <h2><button onClick={()=>handleScroll('main',0)}>About .</button></h2>
                        <h2><button onClick={()=>handleScroll('projectAll', 0)}>Projects .</button></h2>
                        <h2><button onClick={()=>handleScroll('skillsAll', 0)}>Skills .</button></h2>
                        <h2><button onClick={()=>{window.open('https://blog.naver.com/rjqnrdl-')}
                        }>Blog .</button></h2>
                        <h2><button onClick={()=>{window.open('https://github.com/Yoong5g5')}}>GitHub .</button></h2>
                    </div>
                    <div className="aboutBox">
                        <p>g5g5_@naver.com</p>
                        <p>010-6649-6543</p>
                        <p className='update'>Last update:2024/06/21</p>
                    </div>
                </div>
                <div className="textWrap col-9">
                    <h1>  <br/><br/> <span><strong></strong></span>.</h1>
                    <p>
                           .<br/>
                               .<br/>
                         ,      180+  .
                    </p>
                </div>
            </div>
        </div>
    )
}